import { createContext, useContext, useState, ReactNode } from "react";

// 1. Создаём контекст
const RoleContext = createContext<{ role: string; setRole: (role: string) => void } | null>(null);

// 2. Делаем провайдер (обёртку), которая хранит роль
export function RoleProvider({ children }: { children: ReactNode }) {
  const [role, setRole] = useState("guest"); // по умолчанию "guest"

  return <RoleContext.Provider value={{ role, setRole }}>{children}</RoleContext.Provider>;
}

// 3. Делаем хук для удобного использования
export function useRole() {
  const context = useContext(RoleContext);
  if (!context) {
    throw new Error("useRole должен использоваться внутри RoleProvider");
  }
  return context;
}
